body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.encabezado{
  display: flex;
  flex-flow: row nowrap;
  background-color: black;
  color: white;
  font-weight: bold;
  padding: 30px;
 align-items: center;
}

 .texto-tienda{
  flex: 1 1 auto;
 } 
 
 .buscador{
display: flex;
justify-content: flex-end;
flex: 1 1 auto;
 }

 .buscador input{
   padding: 10px;
   border: 1px solid white;
   border-radius: 10px;
 }

 .imagenes{
   display: flex;
   flex-flow: row wrap;
   width: 100%;
 }

 .imagenes .card{
  max-width: 30%;
   flex: 1 1 28%;
   display: flex;
   flex-flow: column nowrap;
   justify-content: center;
   margin: 20px;
   border: 1px solid grey;
 }

  .card img{
    width: 100%;
  }

  .caption{
   padding: 20px;
  }

  .caption p span{
    font-weight: 900;
  }


 .foo{
   padding: 30px;
   background-color: black;
   color: white;
   text-align: center;
 }